section.welcome {
  background-image: url("/images/office.jpg");
  width: 100%;
  height: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}
section.welcome .container-welcome {
  width: 50%;
  height: 50%;
  background-color: pink;
  position: relative;
  display: flex;
  flex-direction: column;
}
section.welcome .container-welcome .welcome-text {
  width: 100%;
  height: 10%;
  background-color: blue;
  color: white;
}
section.welcome .container-welcome .buy {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
}
section.welcome .container-welcome .sell {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
}
section.welcome .container-welcome .rent {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
}
section.welcome .container-welcome .logo-container-welcome {
  width: 40%;
  height: 50%;
  background-color: pink;
}
section.welcome .container-welcome .contact-btn {
  position: absolute;
  top: 96%;
  left: 50%;
  border: none;
  background-color: orangered;
  cursor: pointer;
  border-radius: 20px;
  padding: 10px 15px;
}
section.welcome .container-welcome .contact-btn:hover {
  background-color: orangered;
}